.pagination {
    margin-top: 12px;

    & ul {
        & li {
            & button, & div {
                color: #FFD700;
            }
            & .Mui-selected {
                background-color: #FFD700;
                color: #1C1C1C;

                &:hover {
                    background-color: #FFD700;
                    color: #1C1C1C;
                }
            }
        }
    }

}
